import { db, Timestamp, COLLECTIONS } from '@/lib/firebase';
import { Invoice, User } from '@/types/models';
import { logDbOperation, logInfo, logError } from '@/utils/logger';

/**
 * Behavior Analysis Service - Analyze user patterns and predict churn
 *
 * Analyzes:
 * - Invoicing patterns (frequency, amounts, timing)
 * - Payment patterns (reliability, average days)
 * - Engagement (feature usage, login frequency)
 * - Churn risk prediction
 */

/**
 * Analyze comprehensive user behavior
 */
export async function analyzeUserBehavior(userId: string): Promise<{
  invoicingPattern: any;
  paymentPattern: any;
  engagementScore: number;
  churnRisk: { risk: 'high' | 'medium' | 'low'; score: number };
}> {
  const startTime = Date.now();

  try {
    const [invoicingPattern, paymentPattern, engagementScore, churnRisk] = await Promise.all([
      calculateInvoicingPattern(userId),
      calculatePaymentPattern(userId),
      calculateEngagementScore(userId),
      predictChurn(userId),
    ]);

    logDbOperation('analyze_user_behavior', COLLECTIONS.USER_BEHAVIOR_PROFILE, userId, Date.now() - startTime);

    return {
      invoicingPattern,
      paymentPattern,
      engagementScore,
      churnRisk,
    };
  } catch (error) {
    logError('Failed to analyze user behavior', error);
    throw error;
  }
}

/**
 * Calculate invoicing pattern for a user
 */
export async function calculateInvoicingPattern(userId: string): Promise<{
  frequency: string;
  avgAmount: number;
  topClients: string[];
}> {
  const startTime = Date.now();

  try {
    // Get all invoices for user (last 6 months)
    const sixMonthsAgo = new Date();
    sixMonthsAgo.setMonth(sixMonthsAgo.getMonth() - 6);

    const invoicesQuery = await db
      .collection(COLLECTIONS.INVOICES)
      .where('freelancerId', '==', userId)
      .where('createdAt', '>=', Timestamp.fromDate(sixMonthsAgo))
      .get();

    const invoices = invoicesQuery.docs.map((doc) => doc.data() as Invoice);

    if (invoices.length === 0) {
      return {
        frequency: 'inactive',
        avgAmount: 0,
        topClients: [],
      };
    }

    // Calculate average amount
    const avgAmount = invoices.reduce((sum, inv) => sum + inv.amount, 0) / invoices.length;

    // Calculate frequency (invoices per week)
    const oldestInvoice = invoices[invoices.length - 1];
    const daysSinceFirstInvoice = Math.max(
      1,
      Math.floor((Date.now() - oldestInvoice.createdAt.toDate().getTime()) / (1000 * 60 * 60 * 24))
    );
    const weeksActive = Math.max(1, daysSinceFirstInvoice / 7);
    const invoicesPerWeek = invoices.length / weeksActive;

    let frequency: string;
    if (invoicesPerWeek >= 3) frequency = 'very_active';
    else if (invoicesPerWeek >= 1) frequency = 'active';
    else if (invoicesPerWeek >= 0.5) frequency = 'moderate';
    else frequency = 'low';

    // Get top 3 clients
    const clientCounts: Record<string, number> = {};
    invoices.forEach((inv) => {
      clientCounts[inv.clientName] = (clientCounts[inv.clientName] || 0) + 1;
    });

    const topClients = Object.entries(clientCounts)
      .sort((a, b) => b[1] - a[1])
      .slice(0, 3)
      .map(([name]) => name);

    logDbOperation('calculate_invoicing_pattern', COLLECTIONS.INVOICES, userId, Date.now() - startTime);

    return {
      frequency,
      avgAmount: Math.round(avgAmount),
      topClients,
    };
  } catch (error) {
    logError('Failed to calculate invoicing pattern', error);
    throw error;
  }
}

/**
 * Calculate payment pattern for a user
 */
export async function calculatePaymentPattern(userId: string): Promise<{
  avgDays: number;
  consistency: string;
  reliability: number;
}> {
  const startTime = Date.now();

  try {
    // Get paid invoices (last 6 months)
    const sixMonthsAgo = new Date();
    sixMonthsAgo.setMonth(sixMonthsAgo.getMonth() - 6);

    const invoicesQuery = await db
      .collection(COLLECTIONS.INVOICES)
      .where('freelancerId', '==', userId)
      .where('status', '==', 'paid')
      .where('paidAt', '>=', Timestamp.fromDate(sixMonthsAgo))
      .get();

    const paidInvoices = invoicesQuery.docs.map((doc) => doc.data() as Invoice);

    if (paidInvoices.length === 0) {
      return {
        avgDays: 0,
        consistency: 'insufficient_data',
        reliability: 0,
      };
    }

    // Calculate average days to payment
    const paymentDays = paidInvoices.map((inv) => {
      const dueDate = inv.dueDate.toDate();
      const paidDate = inv.paidAt!.toDate();
      return Math.floor((paidDate.getTime() - dueDate.getTime()) / (1000 * 60 * 60 * 24));
    });

    const avgDays = paymentDays.reduce((sum, days) => sum + days, 0) / paymentDays.length;

    // Calculate consistency (standard deviation)
    const variance =
      paymentDays.reduce((sum, days) => sum + Math.pow(days - avgDays, 2), 0) / paymentDays.length;
    const stdDev = Math.sqrt(variance);

    let consistency: string;
    if (stdDev < 3) consistency = 'very_consistent';
    else if (stdDev < 7) consistency = 'consistent';
    else if (stdDev < 14) consistency = 'variable';
    else consistency = 'unpredictable';

    // Calculate reliability (percentage paid on time)
    const onTimePayments = paymentDays.filter((days) => days <= 7).length;
    const reliability = (onTimePayments / paidInvoices.length) * 100;

    logDbOperation('calculate_payment_pattern', COLLECTIONS.INVOICES, userId, Date.now() - startTime);

    return {
      avgDays: Math.round(avgDays * 10) / 10,
      consistency,
      reliability: Math.round(reliability),
    };
  } catch (error) {
    logError('Failed to calculate payment pattern', error);
    throw error;
  }
}

/**
 * Calculate engagement score (0-100)
 */
export async function calculateEngagementScore(userId: string): Promise<number> {
  const startTime = Date.now();

  try {
    // Get user events (last 30 days)
    const thirtyDaysAgo = new Date();
    thirtyDaysAgo.setDate(thirtyDaysAgo.getDate() - 30);

    const eventsQuery = await db
      .collection(COLLECTIONS.USER_EVENTS)
      .where('userId', '==', userId)
      .where('timestamp', '>=', Timestamp.fromDate(thirtyDaysAgo))
      .get();

    const events = eventsQuery.docs.map((doc) => doc.data());

    if (events.length === 0) {
      return 0; // No engagement
    }

    // Calculate score based on event types
    let score = 0;

    const loginEvents = events.filter((e) => e.eventType === 'login').length;
    const invoiceEvents = events.filter((e) => e.eventType === 'invoice_created').length;
    const featureEvents = events.filter((e) => e.eventType === 'feature_used').length;

    // Score components
    score += Math.min(loginEvents * 2, 30); // Max 30 points for logins
    score += Math.min(invoiceEvents * 5, 40); // Max 40 points for invoices
    score += Math.min(featureEvents * 3, 30); // Max 30 points for feature usage

    logDbOperation('calculate_engagement_score', COLLECTIONS.USER_EVENTS, userId, Date.now() - startTime);

    return Math.min(score, 100); // Cap at 100
  } catch (error) {
    logError('Failed to calculate engagement score', error);
    return 0;
  }
}

/**
 * Predict churn risk for a user
 */
export async function predictChurn(userId: string): Promise<{
  risk: 'high' | 'medium' | 'low';
  score: number;
}> {
  const startTime = Date.now();

  try {
    // Get user data
    const userDoc = await db.collection(COLLECTIONS.USERS).doc(userId).get();
    if (!userDoc.exists) {
      return { risk: 'high', score: 100 };
    }

    const user = userDoc.data() as User;

    // Calculate churn indicators
    let churnScore = 0;

    // 1. Days since last login (max 30 points)
    const daysSinceLogin = user.lastLoginAt
      ? Math.floor((Date.now() - user.lastLoginAt.toDate().getTime()) / (1000 * 60 * 60 * 24))
      : 999;

    if (daysSinceLogin > 30) churnScore += 30;
    else if (daysSinceLogin > 14) churnScore += 20;
    else if (daysSinceLogin > 7) churnScore += 10;

    // 2. Engagement score (max 30 points)
    const engagementScore = await calculateEngagementScore(userId);
    churnScore += Math.max(0, 30 - engagementScore / 3);

    // 3. Invoicing activity (max 40 points)
    const invoicingPattern = await calculateInvoicingPattern(userId);
    if (invoicingPattern.frequency === 'inactive') churnScore += 40;
    else if (invoicingPattern.frequency === 'low') churnScore += 30;
    else if (invoicingPattern.frequency === 'moderate') churnScore += 15;

    logDbOperation('predict_churn', COLLECTIONS.USERS, userId, Date.now() - startTime);

    // Determine risk level
    let risk: 'high' | 'medium' | 'low';
    if (churnScore >= 60) risk = 'high';
    else if (churnScore >= 30) risk = 'medium';
    else risk = 'low';

    return {
      risk,
      score: Math.round(churnScore),
    };
  } catch (error) {
    logError('Failed to predict churn', error);
    return { risk: 'high', score: 100 };
  }
}

/**
 * Generate personalized recommendation for user
 */
export async function generateRecommendation(userId: string): Promise<string> {
  try {
    const behavior = await analyzeUserBehavior(userId);

    // Generate recommendation based on patterns
    if (behavior.churnRisk.risk === 'high') {
      return 'We noticed you haven\'t been active recently. Check out our new features to streamline your invoicing!';
    }

    if (behavior.engagementScore < 30) {
      return 'Try enabling automated collections to save time and get paid faster.';
    }

    if (behavior.paymentPattern.avgDays > 14) {
      return 'Your clients take longer than average to pay. Consider sending reminders earlier.';
    }

    if (behavior.invoicingPattern.frequency === 'low') {
      return 'Boost your earnings by invoicing more regularly. Set up recurring invoices for repeat clients.';
    }

    return 'You\'re doing great! Keep up the consistent invoicing.';
  } catch (error) {
    logError('Failed to generate recommendation', error);
    return '';
  }
}
