import { NextRequest, NextResponse } from 'next/server';
import { db, Timestamp, COLLECTIONS } from '@/lib/firebase';
import { analyzeUserBehavior } from '@/services/behaviorAnalysisService';
import { logInfo, logError } from '@/utils/logger';

export const dynamic = 'force-dynamic';

/**
 * POST /api/cron/analyze-behavior
 * Cron job to analyze user behavior and update profiles
 *
 * Schedule: Daily at 2:00 AM (after collections processing)
 * Vercel Cron: 0 2 * * *
 *
 * Security: Requires CRON_SECRET in Authorization header
 */
export async function POST(request: NextRequest) {
  const startTime = Date.now();

  try {
    // Verify cron secret
    const authHeader = request.headers.get('authorization');
    if (authHeader !== `Bearer ${process.env.CRON_SECRET}`) {
      return NextResponse.json({ error: 'Unauthorized' }, { status: 401 });
    }

    logInfo('Starting behavior analysis cron job');

    // Get all active users from last 30 days
    const thirtyDaysAgo = new Date();
    thirtyDaysAgo.setDate(thirtyDaysAgo.getDate() - 30);

    const usersQuery = await db
      .collection(COLLECTIONS.USERS)
      .where('isActive', '==', true)
      .where('lastActiveAt', '>=', Timestamp.fromDate(thirtyDaysAgo))
      .get();

    logInfo(`Found ${usersQuery.size} active users to analyze`);

    let analyzed = 0;
    let errors = 0;

    // Process users in batches of 50
    const batchSize = 50;
    const userIds = usersQuery.docs.map((doc) => doc.id);

    for (let i = 0; i < userIds.length; i += batchSize) {
      const batch = userIds.slice(i, i + batchSize);

      await Promise.all(
        batch.map(async (userId) => {
          try {
            // Analyze user behavior
            const behavior = await analyzeUserBehavior(userId);

            // Update or create behavior profile
            await db.collection(COLLECTIONS.USER_BEHAVIOR_PROFILE).doc(userId).set(
              {
                userId,
                lastUpdated: Timestamp.now(),
                invoicing: {
                  averagePerMonth: 0, // Calculated from invoicingPattern
                  averagePerWeek: 0,
                  daysOfWeekPreferred: [],
                  timeOfDayPreferred: '',
                  averageAmount: behavior.invoicingPattern.avgAmount,
                  lastInvoiceDate: Timestamp.now(),
                  invoicingGaps: [],
                },
                payments: {
                  averageDaysToPayment: behavior.paymentPattern.avgDays,
                  bestPayingClients: [],
                  worstPayingClients: [],
                  seasonality: {},
                  paymentReliability: behavior.paymentPattern.reliability,
                },
                engagement: {
                  averageOpenPerWeek: 0,
                  lastOpenDate: Timestamp.now(),
                  daysOfWeekMostActive: [],
                  featureUsage: {},
                },
                currentContext: {
                  daysWithoutInvoice: 0,
                  invoiceDebtStatus: '',
                  recentSuccesses: 0,
                  churnRiskScore: behavior.churnRisk.score,
                },
              },
              { merge: true }
            );

            analyzed++;
          } catch (error) {
            logError(`Failed to analyze user ${userId}`, error);
            errors++;
          }
        })
      );
    }

    // Update daily summary
    const today = new Date().toISOString().split('T')[0];
    await db.collection(COLLECTIONS.DAILY_SUMMARIES).doc(today).set({
      date: today,
      activeUsers: userIds.length,
      usersAnalyzed: analyzed,
      analysisErrors: errors,
      timestamp: Timestamp.now(),
    });

    const duration = Date.now() - startTime;
    logInfo(`Behavior analysis complete`, {
      analyzed,
      errors,
      duration: `${(duration / 1000).toFixed(2)}s`,
    });

    return NextResponse.json({
      success: true,
      analyzed,
      errors,
      duration,
    });
  } catch (error) {
    logError('Behavior analysis cron job failed', error);
    return NextResponse.json(
      { error: 'Failed to analyze behavior' },
      { status: 500 }
    );
  }
}
